@import url(https://fonts.googleapis.com/css?family=Nunito);

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h2 {
  font-family: "filson-soft",sans-serif !important;
}

h1 {
  font-family: "maple-web",sans-serif !important;
}


:root {
  --growth: 0
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #ffcd6c ;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header2 {
  background-color: purple ;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.column {
  float: left;
  width: 33.33%;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}


.App-header3 {
  background-color: #F4EAD5 ;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.App-link {
  color: #61dafb;
}


@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.lds-circle {
  display: inline-block;
  width: 51px;
  height: 51px;
  margin: 6px;
  border-radius: 50%;
  position: -webkit-sticky;
  position: sticky;
  top: 45vh;
  background:#FF9022;
  -webkit-animation: lds-circle 2.4s cubic-bezier(0, 0.2, 0.8, 1) infinite;
          animation: lds-circle 2.4s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
@-webkit-keyframes lds-circle {
  0%, 100% {
    -webkit-animation-timing-function: cubic-bezier(0.5, 0, 1, 0.5);
            animation-timing-function: cubic-bezier(0.5, 0, 1, 0.5);
  }
  0% {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
  }
  50% {
    -webkit-transform: rotateY(1800deg);
            transform: rotateY(1800deg);
    -webkit-animation-timing-function: cubic-bezier(0, 0.5, 0.5, 1);
            animation-timing-function: cubic-bezier(0, 0.5, 0.5, 1);
  }
  100% {
    -webkit-transform: rotateY(3600deg);
            transform: rotateY(3600deg);
  }
}
@keyframes lds-circle {
  0%, 100% {
    -webkit-animation-timing-function: cubic-bezier(0.5, 0, 1, 0.5);
            animation-timing-function: cubic-bezier(0.5, 0, 1, 0.5);
  }
  0% {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
  }
  50% {
    -webkit-transform: rotateY(1800deg);
            transform: rotateY(1800deg);
    -webkit-animation-timing-function: cubic-bezier(0, 0.5, 0.5, 1);
            animation-timing-function: cubic-bezier(0, 0.5, 0.5, 1);
  }
  100% {
    -webkit-transform: rotateY(3600deg);
            transform: rotateY(3600deg);
  }
}

.notification {
  color: #FF6837;
  background-color: #ffcd6c !important;
  box-shadow: none !important;
  font-size: 20px;
}

.wrapper {
  display: grid;
  grid-template-columns: 100px 100px 100px;
  grid-gap: 10px;
  background-color: #fff;
  color: #444;
}

.box {
  background-color: #444;
  color: #fff;
  border-radius: 5px;
  padding: 20px;
  font-size: 150%;
}
.wrapper {
  position: relative;
  top: 50px;
  -webkit-transform: translateX(51% - 60px);
          transform: translateX(51% - 60px);
}
.expected {
  position: absolute;
  border-left: 5px solid #92007b;
  border-right: 5px solid #92007b;
  height: 750px;
  background-color: #ffcb00;
  margin: 0 auto;
  -webkit-transform: translateX(-5px) rotateX(60deg);
          transform: translateX(-5px) rotateX(60deg);
  -webkit-transform-origin: 0 150px;
          transform-origin: 0 150px;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  width: 310px;
}
.expected:before {
  border: 10px solid #92007b;
  background-color: #ffcb00;
  border-radius: 50%;
  content: '';
  height: 300px;
  left: -5px;
  position: absolute;
  width: 300px;
}
.expected:after {
  border: 10px solid #92007b;
  background-color: #ffcb00;
  border-radius: 50%;
  content: '';
  height: 300px;
  left: -5px;
  position: absolute;
  width: 300px;
}
.expected:before {
  top: -150px;
}
.expected:after {
  bottom: -150px;
}
.collected {
  position: absolute;
  top: 370px;
  margin: 0 auto;
  -webkit-transform: translateX(-0.1px) rotateX(60deg);
          transform: translateX(-0.1px) rotateX(60deg);
  -webkit-transform-origin: 0 150px;
          transform-origin: 0 150px;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}
.collected:before {
  border: 10px solid #ff5b07;
  background-color: #ff5b07;
  border-radius: 50%;
  content: '';
  height: 300px;
  left: -5px;
  position: absolute;
  width: 300px;
  -webkit-transform: translateY(calc(-740px * 0));
          transform: translateY(calc(-740px * 0));
  -webkit-transform: translateY(calc(-740px * var( --growth)));
          transform: translateY(calc(-740px * var( --growth))); /* GROW HERE*/
  z-index: 99999;
}
.collected:after {
  border: 10px solid #ff8b00;
  background-color: #ff8b00;
  border-radius: 50%;
  content: '';
  height: 300px;
  left: -4px;
  position: absolute;
  width: 296.25px;
}
.collected:before {
  top: -150px;
}
.collected:after {
  bottom: -150px;
}
.collected-bg {
  position: absolute;
  height: 428.57143px;
  background-color: #ff8b00;
  margin: 0 auto;
  -webkit-transform: rotateX(60deg) scaleY(calc(1.5 * 0));
          transform: rotateX(60deg) scaleY(calc(1.5 * 0));
  -webkit-transform: rotateX(60deg) scaleY(calc(1.5 * var(--growth)));
          transform: rotateX(60deg) scaleY(calc(1.5 * var(--growth))); /*GROW HERE*/
  -webkit-transform-origin: bottom;
          transform-origin: bottom;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  width: 310px;
}
.collected-bg:before {
  top: -150px;
}
.collected-bg:after {
  bottom: -150px;
}
.wires {
  position: absolute;
  border-left: 6px solid #92007b;
  border-right: 5px solid #92007b;
  height: 750px;
  background-color: rgba(0, 0, 0, 0);
  margin: 0 auto;
  -webkit-transform: translateX(-5px) rotateX(60deg);
          transform: translateX(-5px) rotateX(60deg);
  -webkit-transform-origin: 0 150px;
          transform-origin: 0 150px;
  -webkit-transform-style: preserve-3d;
